
* {
  text-decoration: none;
  margin: 10px;
  font-family: Arial;
  text-align: center;
  list-style: none;
}
header {
  width: 100%;
  height: 200px;
  background-color: rgb(162, 195, 201);
  position: absolute;
  top: 0;
}
header > h1 {
  width: 160px;
  height: 100px;
  float: left;
  padding: 30px 0 0 30px;
}
header >h1> p {
  color: black;
}
header > h1 > a{
  color: rgb(86, 73, 122);
}
header > h1 > a:hover {
  color: white;
}

header > p {
  padding: 120px 0 0 400px;
  float: left;
  font-weight: bold;
  font-size: 1.4em;
  color: rgb(87, 0, 248);
}
nav {
  position: absolute;
  float: left;
  top: 250px;
  width: 100%;
  height: 500px;
  border: 1px solid black;
}
ul > li> a {
  
  color: aliceblue;
  float: left;
  margin-left: 100px;
  padding:25px 150px ;
  background-color: rgb(87, 117, 102);
  border: 1px solid black;
}
ul > li> a:hover {
  cursor: pointer;
  background-color: rgb(255, 255, 255);
  color: rgb(87, 117, 102);
  font-weight: bold;
  font-size: 1.2em;
}
article {
  position: absolute;
  top: 400px;
  left: 700px;
}
img {
  position: relative;
  margin: 0 auto;
  top: 450px;
  padding-bottom: 100px;
}
